@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
*{box-sizing:border-box}body{margin:0 auto;max-width:1200px;font-size:16px;font-family:'Open Sans', sans-serif;color:#555;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1,h2,h3{color:#111;font-family:'Quicksand', sans-serif}img{width:100%}.nav{padding:15px;background:#000;text-align:center}.nav h1{color:#fff;font-size:1.6em}.home .slogan{text-align:center}.home .slogan h2{font-size:1.2em}.home .slogan .free{color:#4BB543;font-size:1.3em}.home .slogan .sellingPoints{display:flex;text-align:center}.home .slogan .sellingPoints div{flex:50% 1;padding:15px}.home .main{background:#f7f7f7;padding:15px 15px 60px}.home .main .dropdown{margin:15px;display:flex;align-items:center;justify-content:space-between}.home .main .dropdown .results{font-size:15px;font-weight:600;color:#111}.home .main select{padding:12px;width:180px;font-size:16px;border-radius:8px;border:1px solid #111;-webkit-transition:all .2s ease-in-out;transition:all .2s ease-in-out;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-image:url(data:image/png;base64,);background-color:#fff;background-size:12px;background-repeat:no-repeat;background-position:right .75em center}.home .main select:focus{color:#111;box-shadow:0.25rem 0.25rem #111;font-weight:600;outline:0;background-size:15px}.home .main .characters{display:flex;flex-wrap:wrap}.card{position:relative;margin:10px;padding:30px;border-radius:8px;-webkit-transition:all .2s ease;transition:all .2s ease;background:#fff;box-shadow:1px 4px 4px -1px rgba(0,0,0,0.15);color:#111}.card span{position:absolute;right:0;padding:6px 10px 8px 16px;background:#8DC8EA;color:#fff;text-transform:uppercase;font-weight:bold;font-size:15px;border-top-left-radius:30px;border-bottom-left-radius:30px}.card img{padding:30px}.card .details p{margin:0}.card .details h2{margin:0;text-transform:uppercase}.card a{padding:15px;margin:10px 0;width:100%;display:block;background:#f7f7f7;border-radius:30px;border:none;text-align:center;text-decoration:none;text-transform:uppercase;font-weight:bold;font-size:14px;color:#111;-webkit-transition:all .2s ease-in-out;transition:all .2s ease-in-out}.card a:hover{box-shadow:0 0 0 2px #000,1px 5px 4px -2px rgba(0,0,0,0.3)}.footer{padding:15px;text-align:center;background:#000;color:#fff}

